@mixin break($point) {
    @if $point==alpha {
        @media (max-width: 1200px) {
            @content;
        }
    }
    @if $point==beta {
        @media (max-width: 1000px) {
            @content;
        }
    }
    @if $point==gamma {
        @media (max-width: 800px) {
            @content;
        }
    }
    @if $point==delta {
        @media (max-width: 500px) {
            @content;
        }
    }
}

.multi-block {
    width: 100%;
    [class*='col-'] {
        float: left;
    }
}

.grid-block {
    overflow: hidden;
}

.col--right {
    float: right;
}

.beta-col--right {
    @include break(beta) {
        float: right;
    }
}

.col-1-1 {
    width: 100%;
}

.col-1-2 {
    width: 50%;
}

.col-1-4 {
    width: 25%;
}

.beta-1-1 {
    @include break(beta) {
        width: 100%;
    }
}

.beta-col-1-2 {
    @include break(beta) {
        width: 50%;
    }
}

.beta-col-1-4 {
    @include break(beta) {
        width: 25%;
    }
}

.gamma-col-1-1 {
    @include break(gamma) {
        width: 100%;
    }
}

.gamma-col-1-2 {
    @include break(gamma) {
        width: 50%;
    }
}

.gamma-col-1-4 {
    @include break(gamma) {
        width: 25%;
    }
}

//block
.block {
    position: relative;
    transition: all 0.25s ease;
    @include break(gamma) {
        //border-top: 4px solid white;
        border-right: 4px solid white;
        border-left: 4px solid white;
        border-bottom: 4px solid white;
    }
}


/* Block Skin */

//block with bg image
.block--img {
    overflow: hidden;
    &:hover {
        .block__bg {
            //transform: scale(1.125);
            @include break(beta) {
                transform: scale(1);
            }
        }
    }
}

//backgrounds colors
.block--brand-1 {
    background: $color-brand-1;
    &:hover {
        background: $color-brand-1-dark;
    }
}

.block--brand-2 {
    background: $color-brand-2;
    &:hover {
        background: $color-brand-2-dark;
    }
}

//blocknav
.block--nav {
    background: white;
    @include break(gamma) {
        display: none;
    }
    &:hover {
        background: none;
    }
}


/* Block Layout*/

//square
.block--square {
    width: 100%;
    padding-bottom: 100%;
}

//rectangle
.block--rectangle {
    width: 100%;
    padding-bottom: 50%;
}

.beta-block--rectangle {
    @include break(beta) {
        width: 100%;
        padding-bottom: 50%;
    }
}

.delta-block--square {
    /*  @include break(delta){
    width: 100%;
    padding-bottom: 100%;
  }*/
}

//split
.block--split {
    //splits the block into 2 by changing .content & .block__bg
}

.block--split {
    .content {
        width: 50%;
    }
}

.block--no-img {
    &[class*='--split'] {
        .content {
            width: 100% !important;
        }
        .block__bg {
            background: none !important;
        }
    }
}

.beta-block--split {
    @include break(beta) {
        .content {
            width: 50%;
        }
    }
}

//no-split
.block--no-split {}

.delta-block--no-split {
    /* @include break(delta){
    .content {
      width: 100%;
    }   
  }*/
}


/* Block Children */

/*
<div class="block">
  <span class="block__bg></span>
  <div class="block__inner">Content</div>
  <a class="block__link" href="#">Link</a>
</div>
*/

//block__bg
.block__bg {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    transform: scale(1);
    transition: all 0.25s ease;
    .block--split & {
        width: 50%;
        margin-left: 50%;
    }
    .beta-block--split & {
        @include break(beta) {
            width: 50%;
            margin-left: 50%;
        }
    }
    .block--img &:after {
        background: rgba(black, 0.5);
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

//block inner
.block__inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

//block inner nav
.block__inner ul {
    padding: 0;
    margin: 0;
    a {
        text-decoration: none;
        text-transform: uppercase;
        color: black;
    }
}

.block__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-indent: -9999px;
    z-index: 1;
}


/* Content - Lives insdie .block__inner */

.content {
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
    color: white;
    overflow: hidden;
    font-size: 16px;
    p {
        font-size: 1.2vw;
        line-height: 140%;
        margin: 0 0 6px 0;
        @include break(beta) {
            font-size: 1.6vw;
        }
        @include break(gamma) {
            font-size: 2.4vw;
        }
        @include break(delta) {
            display: none;
        }
    }
    h3 {
        font-size: 1.8vw;
        margin: 0 0 12px 0;
        line-height: 125%;
        font-weight: 300;
        @include break(beta) {
            font-size: 2.4vw;
        }
        @include break(gamma) {
            font-size: 3.6vw;
        }
        @include break(delta) {
            font-size: 5vw;
        }
    }
    a {
        color: $color-white;
    }
}

.content__inner {
    padding: 1.25vw;
    position: absolute;
    bottom: 0;
    width: 100%;
}

//content goes to top instead of bottom
.content--alt .content__inner {
    top: 0;
}